import {ChangeDetectionStrategy, Component, Input, OnInit} from '@angular/core';
import {Observable} from "rxjs/Observable";

@Component({
  selector: 'ht-analytics-item-load',
  template: `<div class="loading-container" [style.min-height.px]="_minHeight">
  <ng-content></ng-content>
  <div class="loading-page" *ngIf="loading$ | async">
    <div class="icon auto has-text-grey-light">
      <i class="fa fa-circle-o-notch fa-spin fa-3x fa-fw"></i>
    </div>
  </div>
  <div class="loading-page" *ngIf="noData">
    <div class="auto has-text-grey-light is-size-1">
      No Data
    </div>
  </div>
</div>
`,
  styles: [`.loading-page{
  background:#ffffff96;
  position:absolute;
  height:100%;
  width:100%;
  top:0;
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  z-index:600; }

.loading-container{
  position:relative; }
`],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AnalyticsItemLoadComponent implements OnInit {
  @Input() loading$: Observable<boolean>;
  @Input() minHeight: number;
  @Input() noData: boolean = false;
  constructor() { }

  get _minHeight() {
    return this.minHeight || 300
  }

  ngOnInit() {
  }

}
